
import wx from 'weixin-js-sdk'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapGetters,mapActions }from 'vuex'
import empty from '../components/empty'
export default {
name: "collageued",
data(){
return{
useUp:true,
pageSize:10,
onFetching:false,
pullupDefaultConfig:{
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}
}
},
computed:{
...mapGetters(['getptList'])
},
created(){
this.getList(0,'TOGETHER_DONE',0)
},
methods:{
...mapActions(['myOrderGroupList']),
gotoDetail(id){
wx.miniProgram.navigateTo({
url:'/pages/pinTdetail/index?id='+id
})
},
seeOrder(id){

wx.miniProgram.navigateTo({
url:'/pages/webview/index?orderId='+id+'&path=orderDetail'
})
},
goToPt(){
wx.miniProgram.redirectTo({
url:'/pages/groupShopReco/index'
})
},
goToDe(id){
wx.miniProgram.navigateTo({
url:'/pages/goodDetail/index?id='+id +'&fr=l'
})
},
getList(n,x,z){
this.showLoading()
this.myOrderGroupList({flag:z,data:{page:n,pageSize:this.pageSize,status:x},this:this,method:this.myOrderGroupList})
},
addList(arr){
this.$nextTick(() => {
this.$refs.scrollerBottom.donePullup()
this.$refs.scrollerBottom.reset()

})
},
onpulluploading(){
this.$refs.scrollerBottom.reset()
let n =this.getptList.done.lists.length
let max = this.getptList.done.max
if(n < max){
this.getList(Math.ceil(n/this.pageSize),'TOGETHER_DONE',1)
}
else{
this.$nextTick(() => {
this.$refs.scrollerBottom.disablePullup()
})
}
}
},
filters:{
deld(s){
if(!s){
return s
}
let i = s.indexOf('.')
if(i == -1){
return i
}
else {
return s.slice(0,i)
}
}
},
components:{
LoadMore , Scroller ,empty
}
}



